.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 2vmin;
  color: white;
}

.App-link {
  color: #61dafb;
}

.loading-backdrop {
  position: fixed;
  z-index: 999;
  background: #000000eb;
  height: 100vh;
  width: 100vw;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5em;
  color: white;
}

button {
  border: none;
  background: white;
  color: black;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 25px;
  margin: 5px;
  font-size: 2vmin;
  cursor: pointer;
}

button:disabled {
  opacity: 0.6;
  cursor: no-drop;
}

button:disabled:hover {
  background: white;
}

button:hover {
  background: rgb(218, 218, 218);
}

input[type="text"] {
  border-radius: 5px;
  padding: 10px;
  border: none;
}

input[type="text"]:disabled {
  cursor: no-drop;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
